.footer {
  background-color: #0b182d;
  padding: 30px 0;
  &_top {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding-bottom: 20px;
    border-bottom: 1px solid rgba(255, 255, 255, 0.35);
    @media (max-width: 480px) {
      // flex-direction: column;
      // align-items: flex-start;
      flex-wrap: wrap;
    }
  }
  &_bottom {
    display: flex;
    padding-top: 14px;
    justify-content: space-between;
    align-items: center;
    color: #ffffff;
    font-size: 0.8125rem;
  }
}

.f-logo {
  @media (max-width: 480px) {
    margin-bottom: 30px;
  }
}
.f-contacts {
  @media (max-width: 480px) {
    width: 140px;
    margin-bottom: 30px;
  }
  &_address, &_phone {
    color: #fefeff;
    font-size: 1rem;
    display: block;
    padding-left: 30px;
    position: relative;
    &:before {
      content: '';
      display: block;
      background-repeat: no-repeat;
      background-position: center;
      background-size: cover;
      position: absolute;
      left: 0;
    }
  }
  &_address {
    margin-bottom: 10px;
    &:before {
      width: 12px;
      height: 15px;
      background-image: url(../images/sprite.svg#address);
    }
  }
  &_phone {
    text-decoration: none;
    &:before {
      background-image: url(../images/sprite.svg#phone);
      width: 9px;
      height: 15px;
      top: 1px;
      left: 1px;
    }
  }
}

.f-phone {
  text-align: right;
  @media (max-width: 480px) {
    text-align: left;
    width: 100%;
  }
  &_phone {
    display: block;
    margin-bottom: 6px;
    color: #fefeff;
    font-size: 1rem;
    text-decoration: none;
  }
}

.dev {
  display: flex;
  align-items: center;
  @media (max-width: 480px) {
    display: block;
  }
  span {
    margin-right: 30px;
    @media (max-width: 480px) {
      margin-right: 0;
      margin-bottom: 10px;
      display: block;
    }
  }
}
