* {
	outline: none;
	box-sizing: border-box;
}
*::before, *::after {
	box-sizing: border-box;
}
html {
	font-size: 16px;
	@media (max-width: 1024px) {
		font-size: 15px;
	}
	@media (max-width: 768px) {
		font-size: 14px;
	}
	@media (max-width: 480px) {
		font-size: 12px;
	}
}
body {
	background-color: #fff;
	margin: 0;
	min-height: 100vh;
	scroll-behavior: smooth;
	font-family: 'Roboto', sans-serif;
	font-weight: 300;
}
ul[class], ol[class] {
	list-style: none;
	padding: 0;
	margin: 0;
}
h1[class], h2[class], h3[class], h4[class], h5[class] {
	margin-top: 0;
	font-family: 'Roboto Slab', serif;
}
.uppercase {
	text-transform: uppercase;
}
img {
	max-width: 100%;
	display: block;
}
input, button, textarea, select {
	font: inherit;
}

.container {
	max-width: $container;
	width: 100%;
	margin: 0 auto;
}
.content-wrap {
  max-width: $content;
  margin: 0 auto;
	@media (max-width: 1200px) {
		padding: 0 10px;
	}
}

.link {
  color: $accent;
	font-size: 12px;
  &:hover {
    text-decoration: none;
  }
}
.section {
	padding: 60px 0;
	@media (max-width: 480px) {
		padding: 30px 0px;
	}
}

.btn {
	color: $accent;
	border: 2px solid $accent;
	width: 172px;
	height: 48px;
	background-color: transparent;
	display: flex;
	align-items: center;
	justify-content: center;
	cursor: pointer;
	position: relative;
	transition: all 300ms ease;
	text-decoration: none;
	font-size: 14px;
	font-weight: 500;
	&:after {
		content: '';
		width: 10px;
		height: 7px;
		background-image: url(../images/sprite.svg#arrow-next);
		background-repeat: no-repeat;
		background-position: center;
		background-size: cover;
		display: block;
		margin-left: 32px;
		transition: all 0.2s ease;
	}
	&:hover {
		&:after {
			transform: translateX(6px);
		}
	}
	@media (max-width: 480px) {
		min-width: 170px;
	}
}

.breadcrumb {
	&_item {
		color: #00a6f1;
		font-size: 0.875rem;
		padding: 0px 20px;
		position: relative;
		@media (max-width: 480px) {
			padding: 0 12px;
		}
		&:hover {
			text-decoration: none;
		}
		&:after {
			content: '';
			display: block;
			width: 1px;
			height: 15px;
			background-color: #00a6f1;
			position: absolute;
			right: 0;
			top: 0px;
		}
		&:first-child {
			padding-left: 0;
		}
		&:last-child {
			pointer-events: none;
			color: #fff;
			text-decoration: none;
			&:after {
				display: none;
			}
		}
	}
}

.nav-icon {
	cursor: pointer;
	margin-left: 30px;
	display: none;
  z-index: 120;
	padding-right: 20px;
	@media (max-width: 1160px) {
		display: block;
	}
	@media (max-width: 768px) {
		margin-left: 0;
	}
  &.active {
    .nav-icon_line {
			background-color: #fff;
      &:nth-child(1) {
        transform: rotate(45deg) translate(0px, 3px);
      }
      &:nth-child(2) {
        display: none;
      }
      &:nth-child(3) {
        transform: rotate(-45deg) translate(1px, -5px);
      }
    }
  }
	&_line {
		width: 20px;
		height: 1px;
		margin-bottom: 5px;
		background-color: #000611;
    transition: all 300ms cubic-bezier(0.175, 0.885, 0.32, 1.275);
		&:last-child {
      margin-bottom: 0;
    }
	}
}
.wow {
	visibility: hidden;
}
