.header {
  padding: 17px 0px 0px 0px;
  @media (max-width: 1160px) {
    padding-bottom: 17px;
  }
  &_wrap {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
  &_right {
    display: flex;
    align-items: center;
  }
}

.h-logo {
  margin-right: 30px;
}

.nav {
  margin-right: 30px;
  @media (max-width: 1160px) {
    position: absolute;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    background-color: #011934;
    margin-right: 0;
    z-index: 20;
    padding: 80px 0;
    visibility: hidden;
    opacity: 0;
    transform: scale(0);
    transform-origin: top right;
    transition: all 0.4s ease;
  }
  &.active {
    visibility: visible;
    opacity: 1;
    transform: scale(1);
  }
  &_list {
    display: flex;
    @media (max-width: 1160px) {
      flex-direction: column;
    }
  }
  &_item {
    position: relative;
    &:hover {
      .dropdown {
        opacity: 1;
        visibility: visible;
        transform: scale(1);
        animation: fadeInUp 400ms;
        @media (max-width: 1160px) {
          animation: none;
        }
      }
      .nav_link {
        color: $accent;
        &:after {
          opacity: 1;
        }
        @media (max-width: 1160px) {
          &:after {
            opacity: 0;
          }
        }
      }
    }
  }
  &_link {
    color: #000611;
    font-size: 1rem;
    font-weight: 300;
    text-decoration: none;
    padding: 39px 18px;
    display: block;
    white-space: nowrap;
    position: relative;
    @media (max-width: 1160px) {
      padding: 10px 20px;
      color: #fff;
      font-size: 1.125rem;
    }
    &:after {
      content: '';
      height: 2px;
      width: 100%;
      background-color: $accent;
      display: block;
      position: absolute;
      bottom: 0;
      left: 0;
      opacity: 0;
      transition: all 0.2s ease;
    }
  }
}

.dropdown {
  background-color: #011934;
  position: absolute;
  top: 100%;
  opacity: 0;
  visibility: hidden;
  transform: scale(0);
  padding: 20px 0;
  @media (max-width: 1160px) {
    position: relative;
    top: auto;
    opacity: 1;
    visibility: visible;
    transform: scale(1);
    padding: 0;
  }
  &_link {
    font-size: 1rem;
    color: #00a3f1;
    display: block;
    padding: 12px 30px;
    text-decoration: none;
    white-space: nowrap;
    @media (max-width: 1160px) {
      padding: 5px 30px;
      color: #fff;
      opacity: 0.5;
      transition: all 0.3s ease;
    }
    &:hover {
      background-color: #fff;
      @media (max-width: 1160px) {
        background: none;
        transform: translateX(10px);
      }
    }
  }
}

.search {
  display: block;
  margin-right: 33px;
  height: 42px;
  width: 65px;
  border-left: 2px solid rgba(0, 0, 0, 0.17);
  border-right: 2px solid rgba(0, 0, 0, 0.17);
  position: relative;
  &:after {
    content: '';
    width: 13px;
    min-width: 13px;
    height: 15px;
    background-image: url(../images/sprite.svg#search);
    background-repeat: no-repeat;
    background-position: center;
    background-size: cover;
    display: block;
    position: absolute;
    top: 50%;
    left: 50%;
    margin-top: -7px;
    margin-left: -7px;
  }
}

.h-contacts {
  text-align: right;
  @media (max-width: 768px) {
    display: none;
  }
  &_phone {
    display: block;
    text-decoration: none;
    color: #000611;
    font-size: 16px;
    font-weight: 300;
  }
}
