.inner-head {
  background-color: #000000;
  height: 289px;
  padding: 24px 0;
  color: #fff;
  @media (max-width: 900px) {
    height: auto;
  }
  &_wrap {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 30px 0;
    @media (max-width: 768px) {
      display: block;
    }
  }
  &_title {
    font-size: 3rem;
    max-width: 420px;
    width: 50%;
    font-weight: 400;
    @media (max-width: 768px) {
      width: 100%;
    }
    @media (max-width: 480px) {
      font-size: 2.5rem;
    }
  }
  &_subtitle {
    font-size: 1.5rem;
    font-family: 'Roboto Slab', serif;
    // border: 5px solid #00a6f1;
    width: 50%;
    margin-left: 20px;
    position: relative;
    &:before {
      content: '';
      width: 5px;
      display: block;
      position: absolute;
      left: 0;
      bottom: 0;
      background-color: $accent;
      animation: 0.5s ease 0.6s alternate showVerticalBorder;
      animation-fill-mode: forwards;
      @media (max-width: 768px) {
        width: 3px;
      }
    }
    &:after {
      content: '';
      width: 5px;
      display: block;
      position: absolute;
      right: 0;
      top: 0;
      background-color: $accent;
      animation: 0.5s ease 0.6s alternate showVerticalBorder;
      animation-fill-mode: forwards;
      @media (max-width: 768px) {
        width: 3px;
      }
    }
    span {
      display: block;
      position: relative;
      padding: 15px 50px 15px 30px;
      @media (max-width: 480px) {
        padding: 10px 20px;
      }
      &:before {
        content: '';
        height: 5px;
        display: block;
        position: absolute;
        left: 0;
        bottom: 0;
        background-color: $accent;
        animation: 0.6s ease showHorizontalBorder;
        animation-fill-mode: forwards;
        @media (max-width: 768px) {
          height: 3px;
        }
      }
      &:after {
        content: '';
        height: 5px;
        display: block;
        position: absolute;
        right: 0;
        top: 0;
        background-color: $accent;
        animation: 0.6s ease showHorizontalBorder;
        animation-fill-mode: forwards;
        @media (max-width: 768px) {
          height: 3px;
        }
      }
    }
    @media (max-width: 768px) {
      width: 100%;
      margin-left: 0;
    }
    @media (max-width: 480px) {
      font-size: 1.375rem;
    }
  }
}

@keyframes showHorizontalBorder {
  from {
    width: 0%;
    opacity: 0;
  }
  to {
    width: 100%;
    opacity: 1;
  }
}
@keyframes showVerticalBorder {
  from {
    height: 0%;
    opacity: 0;
  }
  to {
    height: 100%;
    opacity: 1;
  }
}

.inner-page {
  padding: 78px 0 0 0;
  color: #5e6a78;
  font-size: 1.125rem;
  line-height: 1.5rem;
  font-weight: 300;
  @media (max-width: 480px) {
    padding-top: 30px;
  }
}

.inner-title {
  color: #020200;
  font-size: 1.875rem;
  line-height: 2rem;
  font-weight: 400;
  margin-bottom: 40px;
  @media (max-width: 480px) {
    margin-bottom: 30px;
  }
}

.i-page-columns {
  padding: 60px 0;
  display: flex;
  justify-content: space-between;
  @media (max-width: 768px) {
    display: block;
    padding: 20px 0;
  }
  &_item {
    width: 48%;
    @media (max-width: 768px) {
      width: 100%;
      margin-bottom: 50px;
    }
  }
}
.i-projects  {
  padding-top: 60px;
}
.simple-list {
  li {
    padding-left: 20px;
    margin-bottom: 15px;
    position: relative;
    @media (max-width: 480px) {
      margin-bottom: 10px;
    }
    &:before {
      content: '';
      width: 5px;
      height: 5px;
      background-color: $accent;
      border-radius: 50%;
      display: block;
      position: absolute;
      left: 0;
      top: 10px;
      @media (max-width: 480px) {
        width: 4px;
        height: 4px;
        top: 8px;
      }
    }
  }
}

.steps {
  &_list {
    display: flex;
    flex-wrap: wrap;
  }
  &_item {
    width: 33%;
    height: 318px;
    background-color: #243b57;
    color: #fff;
    padding: 150px 50px 50px 50px;
    position: relative;
    transition: all .2s ease;
    &:hover {
      background-color: lighten(#243b57, 3%);
    }
    @media (max-width: 1024px) {
      width: 50%;
      margin-bottom: 1px;
    }
    @media (max-width: 768px) {
      height: auto;
    }
    @media (max-width: 480px) {
      width: 100%;
      margin-bottom: 0;
    }
    @media (max-width: 360px) {
      padding: 150px 30px 50px 30px;
    }
    &:nth-child(2n) {
      background-color: #1a2941;
      &:hover {
        background-color: darken(#1a2941, 2%);
      }
    }
  }
  &_number {
    font-size: 60px;
    line-height: 60px;
    font-family: "Roboto Slab";
    font-weight: 400;
    position: absolute;
    right: 50px;
    top: 50px;
    @media (max-width: 480px) {
      font-size: 40px;
      line-height: 40px;
    }
    @media (max-width: 360px) {
      right: 30px;
      top: 30px;
    }
  }
  &_icon {
    position: absolute;
    left: 50px;
    top: 50px;
    @media (max-width: 360px) {
      left: 30px;
      top: 30px;
    }
  }
}


.p-contacts  {
  &_top {
    background-color: #0b182d;
    background-image: url(../images/about_bg.png);
    background-repeat: no-repeat;
    background-position: right 60px top 50px;
    color: #fff;
    padding: 25px 0;
  }
  .breadcrumb {
    margin-bottom: 50px;
  }
  &_title {
    font-size: 3rem;
    font-weight: 300;
    margin-bottom: 60px;
    @media (max-width: 480px) {
      margin-bottom: 30px;
    }
  }
  &_map {
    height: 648px;
    @media (max-width: 768px) {
      height: 550px;
    }
    @media (max-width: 480px) {
      height: 400px;
    }
    img {
      width: 100%;
      height: 100%;
      object-fit: cover;
    }
  }
}
.contacts-list {
  display: flex;
  justify-content: space-between;
  margin-bottom: 50px;
  @media (max-width: 768px) {
    display: block;
  }
  &_item {
    width: 30%;
    max-width: 286px;
    position: relative;
    padding-top: 10px;
    font-size: 1.375rem;
    font-weight: 300;
    color: #fff;
    text-decoration: none;
    display: flex;
    align-items: center;
    @media (max-width: 768px) {
      width: 100%;
      max-width: 100%;
      margin-bottom: 20px;
      padding-left: 20px;
    }
    @media (max-width: 480px) {
      margin-bottom: 10px;
      padding-left: 10px;
      padding-right: 10px;
    }
    &:before {
      content: '';
      display: block;
      background-repeat: no-repeat;
      background-position: center;
      background-size: cover;
      margin-right: 39px;
    }
    &.address:before {
      background-image: url(../images/sprite.svg#p-address);
      width: 46px;
      min-width: 46px;
      height: 60px;
      @media (max-width: 768px) {
        width: 30px;
        min-width: 30px;
        height: 39px;
      }
    }
    &.phone:before {
      background-image: url(../images/sprite.svg#p-phone);
      width: 30px;
      min-width: 30px;
      height: 52px;
      @media (max-width: 768px) {
        width: 30px;
        min-width: 30px;
        height: 36px;
      }
    }
    &.mail:before {
      background-image: url(../images/sprite.svg#p-mail);
      width: 50px;
      min-width: 50px;
      height: 39px;
      @media (max-width: 768px) {
        width: 30px;
        min-width: 30px;
        height: 36px;
      }
    }
  }
}

.blocks-wrap {
  margin: 60px 0 50px 0;
  @media (max-width: 480px) {
    margin: 50px 0px 20px 0px;
  }
}
.blocks {
  display: flex;
  flex-wrap: wrap;
  &_item {
    width: 25%;
    height: 316px;
    position: relative;
    text-align: center;
    background-color: #243b57;
    color: #fff;
    padding: 190px 15px 30px 15px;
    @media (max-width: 1024px) {
      width: 33%;
      height: auto;
    }
    @media (max-width: 480px) {
      width: 50%;
      margin-bottom: 1px;
      padding-top: 140px;
    }
    &:hover {
      background-color: lighten(#243b57, 3%);
    }
    &:nth-child(2),
    &:nth-child(4),
    &:nth-child(5),
    &:nth-child(7)
    {
      background-color: #1a2941;
      @media (max-width: 1024px) {
        background-color: #243b57;
      }
      &:hover {
        background-color: darken(#1a2941, 2%);
        @media (max-width: 1024px) {
          background-color: lighten(#243b57, 3%);
        }
      }
    }
    &:nth-child(2n) {
      @media (max-width: 1024px) {
        background-color: #1a2941;
      }
      &:hover {
        @media (max-width: 1024px) {
          background-color: darken(#1a2941, 2%);
        }
      }
    }
  }
  &_img {
    position: absolute;
    top: 80px;
    left: 50%;
    transform: translateX(-50%);
    @media (max-width: 480px) {
      top: 40px;
    }
  }
}


.text-w-photo {
  padding: 60px 0 20px 0;
  display: flex;
  justify-content: space-between;
  @media (max-width: 768px) {
    display: block;
    padding: 20px 0;
  }
  &_text {
    width: 54%;
    @media (max-width: 768px) {
      width: 100%;
      margin-bottom: 40px;
    }
  }
  &_photo {
    width: 41%;
    @media (max-width: 768px) {
      width: 100%;
    }
  }
}
.production-photo {
  @media (max-width: 768px) {
    padding-top: 0;
    padding-bottom: 0;
  }
  .i-page-columns_item {
    @media (max-width: 768px) {
      margin-bottom: 15px;
    }
    &:last-child {
      @media (max-width: 768px) {
        margin-bottom: 0;
      }
    }
  }
}


.small-list {
  margin-top: 18px!important;
  li {
    padding-left: 15px;
    position: relative;
    &:before {
      content: '';
      width: 4px;
      height: 4px;
      background-color: $accent;
      border-radius: 50%;
      display: block;
      position: absolute;
      left: 0;
      top: 11px;
      @media (max-width: 480px) {
        top: 7px;
      }
    }
  }
}

.metalworking {
  margin-top: 60px;
  margin-bottom: 20px;
  .steps_item {
    height: auto;
  }
}

.product {
  margin-bottom: 40px;
  &_list {
    display: flex;
    flex-wrap: wrap;
    margin-bottom: 20px;
    @media (max-width: 768px) {
      justify-content: space-between;
    }
  }
  &_item {
    width: 30%;
    text-decoration: none;
    color: #000;
    text-align: center;
    display: block;
    margin-bottom: 40px;
    margin-right: 5%;
    transition: all 0.2s ease;
    @media (max-width: 1024px) {
      width: 32%;
      margin-right: 2%;
    }
    @media (max-width: 768px) {
      width: 49%;
      margin-right: 0;
      margin-bottom: 35px;
    }
    @media (max-width: 480px) {
      width: 100%;
      margin-bottom: 30px;
    }
    &:nth-child(3n) {
      margin-right: 0;
    }
    &:hover {
      color: $accent;
      img {
        transform: scale(1.1);
      }
    }
  }
  &_img {
    margin-bottom: 20px;
    background-color: #cfcfcf;
    width: 100%;
    height: 246px;
    overflow: hidden;
    @media (max-width: 768px) {
      margin-bottom: 15px;
    }
    @media (max-width: 480px) {
      height: 220px;
    }
    img {
      width: 100%;
      height: 100%;
      object-fit: cover;
      transition: all 0.2s ease;
    }
  }
  &_title {
    padding: 0 10px;
  }
}

.page-nav {
  display: flex;
  align-items: center;
  justify-content: center;
  &_prev, &_next {
    width: 41px;
    height: 41px;
    background-color: #1A2941;
    display: flex;
    justify-content: center;
    align-items: center;
    border: 2px solid #1A2941;
    transition: all 0.2s ease;
    &:before {
      content: '';
      display: block;
      width: 10px;
      height: 10px;
      background-image: url(../images/sprite.svg#next);
      background-repeat: no-repeat;
      background-position: center;
      background-size: cover;
    }
    &:hover {
      background-color: #fff;
      border-color: $accent;
    }
  }
  &_prev {
    transform: rotate(180deg);
    margin-right: 20px;
  }
  &_next {
    margin-left: 20px;
  }
  &_item {
    text-decoration: none;
    color: #5E6A78;
    padding: 10px;
    &.active {
      color: $accent;
    }
  }
}
