@import 'fonts.scss';
@import 'var.scss';
@import 'general.scss';
@import 'header.scss';
@import 'form.scss';
@import 'footer.scss';
@import 'inner.scss';



.main {
  height: 620px;
  width: 100%;
  background-image: url(../images/main.png);
  background-repeat: no-repeat;
  background-size: cover;
  background-color: #0c386c;
  @media (max-width: 768px) {
    height: 500px;
  }
  @media (max-width: 480px) {
    height: 320px;
    background-image: url(../images/main_mobile.jpg);
  }
  &_wrap {
    height: 100%;
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: space-between;
    @media (max-width: 480px) {
      justify-content: center;
    }
  }
  &_title {
    max-width: 626px;
    color: #ffffff;
    font-size: 3.75rem;
    line-height: 4.375rem;
    font-weight: 400;
    text-transform: uppercase;
    letter-spacing: 0.36px;
    @media (max-width: 1200px) {
      font-size: 3rem;
      line-height: 4rem;
    }
    @media (max-width: 768px) {
      font-size: 2.5rem;
      line-height: 3rem;
      max-width: none;
      border: none;
    }
    @media (max-width: 480px) {
      font-size: 2rem;
      line-height: 2.8rem;
      text-align: center;
    }
    span {
      display: block;
    }
    span.color {
      display: block;
      color: $accent;
      font-weight: 700;
      border-bottom: 6px solid #00aeef;
      @media (max-width: 480px) {
        border-width: 3px;
      }
    }
  }
}

.main-img {
  position: relative;
  z-index: 10;
  &_1 {
    position: absolute;
    left: -15px;
    top: 15px;
  }
  &_2 {
    position: absolute;
    left: -30px;
    top: 30px;
  }
}
.main-img-wrap {
  position: relative;
  padding: 83px 85px;
  @media (max-width: 1024px) {
    display: none;
  }
}
.sequence {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-image: url('../images/sequence.png');
  background-position: -0 -0;
  animation: 15s sequenceAnimated 0s steps(287) infinite,
              3s sequenceOpacity ease 1s;
  opacity: 0;
  animation-fill-mode: forwards;
  z-index: 20;
}

@keyframes sequenceAnimated {
  0% {
    background-position: -0 -0;
  }
  100% {
    background-position: -0 -149814px;
  }
}

@keyframes sequenceOpacity {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}

.services {
  overflow: hidden;
  padding-bottom: 80px;
  @media (max-width: 480px) {
    padding-bottom: 20px;
  }
  &_wrap {
    padding: 67px 0 25px 0;
    position: relative;
    @media (max-width: 768px) {
      padding: 20px 0;
    }
    &.right {
      .services_text {
        padding-right: 230px;
        @media (max-width: 1200px) {
          padding-right: 50px;
        }
        @media (max-width: 768px) {
          padding-right: 0;
        }
      }
      .services_bg {
        right: -30px;
        @media (max-width: 1700px) {
          right: -10%;
        }
        @media (max-width: 1500px) {
          right: -20%;
        }
        @media (max-width: 1200px) {
          right: -40%;
        }
      }
    }
    &.left {
      .services_text {
        padding-left: 230px;
        @media (max-width: 1200px) {
          padding-left: 50px;
        }
        @media (max-width: 768px) {
          padding-left: 0;
        }
      }
      .services_bg {
        left: -30px;
        @media (max-width: 1700px) {
          left: -10%;
        }
        @media (max-width: 1500px) {
          left: -20%;
        }
        @media (max-width: 1200px) {
          left: -40%;
        }
      }
    }
  }
  &_bg {
    position: absolute;
    top: 0;
    z-index: 1;
  }
  &_block {
    display: flex;
    @media (max-width: 768px) {
      flex-direction: column;
    }
  }
  &_text, &_img {
    width: 50%;
    @media (max-width: 768px) {
      width: 100%;
    }
  }
  &_img {
    overflow: hidden;
    z-index: 5;
    img {
      @media (max-width: 768px) {
        width: 100%;
        object-fit: cover;
        height: 400px;
      }
      @media (max-width: 480px) {
        height: 250px;
      }
    }
  }
  &_text {
    padding: 52px 0;
    @media (max-width: 768px) {
      order: -1;
      padding: 40px 0;
    }
  }
  &_title {
    font-size: 1.875rem;
    font-weight: 300;
    margin-bottom: 60px;
    @media (max-width: 480px) {
      margin-bottom: 30px;
    }
  }
  &_description {
    font-weight: 300;
    line-height: 24px;
    margin-bottom: 60px;
    color: #5e6a78;
    font-size: 0.875rem;
    @media (max-width: 480px) {
      margin-bottom: 30px;
    }
  }
}

.about {
  background-color: #0b182d;
  background-image: url(../images/about_bg.png);
  background-repeat: no-repeat;
  background-position: center right;
  padding: 112px 0 100px 0;
  @media (max-width: 768px) {
    padding-bottom: 40px;
  }
  @media (max-width: 480px) {
    padding: 60px 0 0 0;
  }
  &_wrap {
    display: flex;
    @media (max-width: 1024px) {
      display: block;
    }
  }
  &_left, &_right {
    width: 50%;
    @media (max-width: 1024px) {
      width: 100%;
    }
  }
  &_left {
    padding-right: 130px;
    @media (max-width: 1024px) {
      padding-right: 0;
    }
  }
  &_title {
    font-size: 3rem;
    font-weight: 300;
    margin-bottom: 45px;
    color: #fff;
    @media (max-width: 480px) {
      font-size: 2.5rem;
    }
    span {
      color: #00aeef;
      display: block;
    }
  }
  &_description {
    color: #ffffff;
    font-size: 1rem;
    line-height: 1.875rem;
    opacity: 0.57;
    a {
      color: #00aeef;
      &:hover {
        text-decoration: none;
      }
    }
  }
}

.advantages {
  padding: 81px 77px;
  display: flex;
  @media (max-width: 1024px) {
    justify-content: center;
  }
  @media (max-width: 480px) {
    padding: 70px 0px;
    justify-content: space-around;
  }
  &_item {
    max-width: 160px;
    color: #ffffff;
    text-align: center;
    margin-right: 170px;
    padding-top: 170px;
    position: relative;
    &:last-child {
      margin-right: 0;
    }
    @media (max-width: 1200px) {
      margin-right: 100px;
    }
    @media (max-width: 480px) {
      margin-right: 30px;
      padding-top: 140px;
    }
  }
  &_icon {
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    margin: auto;
    @media (max-width: 480px) {
      width: 100px;
    }
  }
  &_title {
    font-family: "Roboto Slab";
    font-size: 1.375rem;
    margin-bottom: 42px;
    display: block;
  }
  &_description {
    opacity: 0.48;
    font-size: 12px;
    line-height: 18px;
    font-weight: 100;
  }
}


.slider {
  position: relative;
  &_item {
    img {
      @media (max-width: 1024px) {
        height: 300px;
      }
    }
  }
}
.slider-controls {
  position: absolute;
  top: 50%;
  margin-top: -20px;
  left: 0;
  width: 100%;
  &_wrap {
    display: flex;
    justify-content: space-between;
    cursor: pointer;
  }
  &_arrow {
    width: 41px;
    height: 41px;
    box-shadow: 0 3px 9px 1px rgba(21, 164, 255, 0.44);
    background-color: #ffffff;
    display: flex;
    align-items: center;
    justify-content: center;
    transition: all 0.2s ease;
    &:hover {
      opacity: 0.8;
    }
    &:after {
      content: '';
      width: 10px;
      height: 10px;
      display: block;
      background-repeat: no-repeat;
      background-position: center;
      background-size: cover;
    }
  }
}
.slider-prev:after {
  background-image: url(../images/sprite.svg#prev);
}
.slider-next:after {
  background-image: url(../images/sprite.svg#next);
}
